import React from 'react';

import styles from './Content.module.scss';
import linkedInLogo from './LinkedIn_Logo.svg';
import gitHubLogo from './GitHub_Logo.svg';
import resumeLogo from './Resume_Logo.svg';

interface LogoButtonProps {
    src: string,
    href: string,
    label: string
}

const LogoButton: React.FC<LogoButtonProps> = props => {
    return (
        <div className={styles.logoButton}>
            <div>
                <a href={props.href} target='_blank' rel='noopener noreferrer'>
                    <img src={props.src} alt='LinkedIn'/>
                    <img src={props.src} alt='' />
                </a>
            </div>
            <p>{props.label}</p>
        </div>
    )
}

const ButtonRow: React.FC = props => {
    return (
        <div className={styles.buttonRow}>
            {props.children}
        </div>
    )
}

const Header: React.FC = props => {
    return (
        <>
            <h1>
                {props.children}
            </h1>
            <br/>
        </>
    )
}

interface SectionProps {
    title: string
}

const Section: React.FC<SectionProps> = props => {
    return (
        <div className={styles.section}>
            <Header>- {props.title} -</Header>
            {props.children}
        </div>
    )
}

const Line: React.FC = props => {
    return (
        <>
            <p style={{textIndent: '3vw'}}>
                {props.children}
            </p>
            <br/>
        </>
    );
}

